
.inline-block {
  display: inline-block;
}
* {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
}
html, body {
  margin: 40px 0 0 0;
}
.deep-gary-title {
  font-size: 23px;
  color: #646464;
  vertical-align: top;
}
#search-bar-container {
  text-align: center;
}
#search-bar-container form {
    position: relative;
    margin: 0px auto;
    width: 500px;
}
#search-city-button {
  color: white;
  margin-left: 5px;
  background-color: #ec7c2a;
}
.search-bar-item-style {
  height: 35px;
  border-radius: 5px;
  vertical-align: middle;
  border: 1px solid #ec7c2a;
  padding: 0 10px;
}
.sample-city-name {
  color: #c3bdb5;
  right: 65px;
  bottom: 5px;
  font-size: 14px;
  position: relative;
  list-style-type: none;
}
.sample-city-name li {
    margin: 10px;
    display: inline;
    cursor: pointer;
}
.sample-city-name li:hover {
      color: #ec7c2a;
      -webkit-transition: .2s;
      transition: .2s;
}
#weather-data-container {
  margin: 50px auto 100px auto;
  width: 1000px;
  min-width: 950px;
}
#forecast-weather-container {
  vertical-align: top;
  min-width: 700px;
}
#fore-weather-title {
  font-size: 23px;
  color: #ec7c2a;
}
#fore-tab-list {
  padding: 0px;
  margin-bottom: 26px;
}
.forecast-tab {
  color: #868686;
  padding: 8px;
  margin: 0px 2px;
  border-bottom: 1px solid #868686;
}
.forecast-tab:hover {
    color: #646464;
    border-bottom: 1px solid #ec7c2a;
    -webkit-transition: 0.5s;
    transition: 0.5s;
}
.forecast-tab:active {
    color: #ec7c2a;
    border-bottom: 1px solid #ec7c2a;
}
#temp-switch-btn {
  position: absolute;
  top: 0px;
  right: 50px;
  width: 60px;
  height: 34px;
}
#temp-switch-btn input {
    display: none;
}
#temp-slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #d5d5d5;
  -webkit-transition: .4s;
  transition: .4s;
}
#temp-slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: #ef9b74;
    -webkit-transition: .4s;
    transition: .4s;
}
#temp-slider.round {
    border-radius: 34px;
}
#temp-slider.round:before {
      border-radius: 50%;
}
#temp-unit {
  font-size: 13px;
  color: white;
  position: absolute;
  left: 10px;
  line-height: 35px;
}
input:checked + #temp-slider:before {
  -webkit-transform: translateX(26px);
  transform: translateX(26px);
}

