  display: inline-block;
}
#fore-weather-subtitle {
  font-size: 18px;
  color: #646464;
}
#chart-container {
  height: 250px;
}
#fore-main-info-container {
  margin-left: 8px;
}
#fore-main-info-container p {
    text-align: center;
    font-size: 11px;
    margin: 2px 0px;
}
#fore-main-info-container .fore-main-info {
    width: 10%;
